table.digital-grid.skin-default{border-collapse:separate;border-spacing:0px}table.digital-grid.skin-default>thead>tr>th{padding:.7rem;font-size:1.1em;background-color:#fff;color:#000;border-bottom:1px solid black}table.digital-grid.skin-default>tbody>tr>td{padding:.5rem .5rem;border-bottom:1px solid #dee2e6;color:#000}table.digital-grid.skin-default>tbody>tr.disabled td,table.digital-grid.skin-default>tbody>tr.disabled td *{color:#bbb}table.digital-grid.skin-default>tbody>tr:nth-of-type(even) td{background-color:#f7f7f7}table.digital-grid.skin-default>tbody>tr.grid-selected td{background-color:#FFFFCC !important}table.digital-grid.skin-default .pagination{color:#000;font-size:1.1em;border-top:1px solid black;width:100%;overflow:hidden;list-style:none;margin:0;padding:0}table.digital-grid.skin-default .pagination li{background-color:#fff;padding:0}table.digital-grid.skin-default .pagination li .page-link{padding:0.4em;line-height:2.4em}table.digital-grid.skin-default .pagination li.first,table.digital-grid.skin-default .pagination li.last,table.digital-grid.skin-default .pagination li.prev,table.digital-grid.skin-default .pagination li.next{cursor:pointer}table.digital-grid.skin-default .pagination li.disabled{opacity:0.5;cursor:default}table.digital-grid.skin-default .pagination div.nowrap{white-space:nowrap}table.digital-grid.skin-default .pagination span::selection,table.digital-grid.skin-default .pagination div::selection{color:none;background:none}
